import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";

const dispatchedEvents = [];

/*
    Example: <srs-code-block html_string="">
            </srs-code-block>
*/
const SrsCodeBlock = function (props, ref) {
    const [copied, setCopied] = useState(false);

    const copyToClipboard = () => {
        const el = document.createElement('textarea');
        el.textContent = props.html_string;
        el.style.position = 'fixed';
        el.style.opacity = 0;
        document.body.appendChild(el);
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
        setCopied(true);
      };
    
      return (
        <div>
          <pre>
            <code dangerouslySetInnerHTML={{ __html: props.html_string }} />
          </pre>
          <button onClick={copyToClipboard}>Copy</button>
          {copied && <span>Copied!</span>}
        </div>
      );
}

const styles = `
    
`
const props = [{
    name: "html_string",
    defaultValue: "",
    type: "string"
}]

const slots = [{name: "default", allowed: []}]; 

const WrappedComponent = wrapper(React.forwardRef(SrsCodeBlock), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };